*{box-sizing:border-box;padding:0;margin:0}:root{--background: white;--foreground: #1a1a1a;--border: rgba(26, 26, 26, 0.2);--nav-background: $white}.dark{--background: #1a1a1a;--foreground: white;--border: rgba(255, 255, 255, 0.2);--nav-background: #272727}body{background-color:var(--background);color:var(--foreground);transition:background-color .3s ease,color .3s ease;font-family:"Helvetica Neue",Helvetica,Arial,sans-serif}a{color:inherit;text-decoration:none}.customFont{font-family:var(--font-custom),"Helvetica",Arial,sans-serif}